*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.game {
    width: 600px;
    height: 225px;
    border: 1px solid black;
    margin-top: 300px;
    margin-left: 500px;
  }
  #dino {
    width: 50px;
    height: 50px;
    background-image: url(img/trex.png);
    background-size: 50px 50px;
    position: relative;
    top: 150px;
  }
  
  .jump {
    animation: jump 0.3s linear;
  }
  
  @keyframes jump {
    0% {
      top: 150px;
    }
  
    30% {
      top: 130px;
    }
  
    50% {
      top: 80px;
    }
  
    80% {
      top: 130px;
    }
  
    100% {
      top: 150px;
    }
  }
  
  #cactus {
    width: 20px;
    height: 40px;
    position: relative;
    top: 110px;
    left: 580px;
    background-image: url("img/cactus.png");
    background-size: 20px 40px;
    animation: block 1.7s infinite linear;
  }
  
  @keyframes block {
    0% {
      left: 580px;
    }
  
    100% {
      left: -5px;
    }
  }
  @media only screen and (max-width:800px) {
    .game{
      margin-top: 200px;
      margin-left: 100px;
    }
  }